.title {
    font-weight: 700;
    font-size: 32px;
    line-height: 40px;
    letter-spacing: -0.005em;
    color: $font-color;
    margin-bottom: 24px;
    &-secondary {
        color: $font-color;
        font-size: 20px;
        line-height: 26px;
        letter-spacing: -0.005em;
    }
    &-small{
        font-size: 16px;
    }
}
.small{
    font-size: 12px;
}
p {
    font-weight: 400;
    font-size: 14px;
    line-height: 170%;
    color: $font-color-secondary;
    &.small{
        font-size: 12px;
        line-height: 20px;
    }
    &.big{
        font-size: 16px;
        line-height: 24px;
        color: $font-color;
    }
}

a{
    color: $success;
}