.card2card {
    padding-top: 20px;
    // padding-bottom: 40px;
    .hr {
        margin-right: -15px;
        margin-bottom: 5px;
        margin-top: 5px;
        color: rgba(143, 145, 153, 0.24);
    }
    .amount-info {
        h5 {
            margin-bottom: 5px;
        }
    }
    .summary-tab {
        .card {
            margin-bottom: 6px;
        }
        .hr {
            margin-top: 10px;
            margin-bottom: 10px;
        }
    }
    .arrow {
        display: flex;
        justify-content: center;
    }
    .safety-card {
        min-height: 100px;
        max-width: 350px;
        h5 {
            font-size: 14px;
            line-height: 24px;
        }
        &-icon {
            padding-top: 5px;
        }
    }
}

@media screen and (min-width: 768px) {
    .card2card {
        .amount-tab,
        .summary-tab {
            .card {
                min-height: initial;
                background-color: initial;
                border: none;
                box-shadow: none;
                padding: 0;
                .controlls-group {
                    padding-right: 56px;
                }
            }
        }
        .summary-tab {
            .h5 {
                margin-bottom: 28px;
            }
            .hr {
                margin-bottom: 34px;
                margin-right: 0px;
            }
        }
    }
}

.cvv-wrapper {
    position: relative;
    .i-info {
        position: absolute;
        top: 1px;
        left: 42px;
    }
}
