.tab {
    display: none;
    &-content {
        @media screen and (min-width: 768px) {
            min-height: 216px;
            background-color: $white;
            border-radius: 12px;
            box-shadow: 0px 2px 6px 0px rgb(19 20 22 / 15%);
            padding: 40px;
            .cards {
                display: flex;
                justify-content: space-between;
                max-width: 846px;
                > div:first-child,
                > div:last-child {
                    flex-basis: 45%;
                }
            }
            .credit-card {
                max-width: 343px;
                min-height: 230px;
                transition:  250ms cubic-bezier(0.075, 0.82, 0.165, 1);
                // display: flex;
                // flex-direction: column;
                // justify-content: space-between;
            }
            .arrow {
                transform: rotate(-90deg);
                margin: 25px;
            }
        }
    }
    &-title {
        display: flex;
        padding-bottom: 24px;
    }
    &-step {
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background-color: $white;
        color: $font-color;
        display: flex;
        align-items: center;
        justify-content: center;
        &.active {
            background-color: $info-ld;
            color: $white;
        }
        &.completed {
            color: transparent;
            background-color: #d7f4ee;
            background-position: center center;
            background-size: 70%;
            background-repeat: no-repeat;
            background-image: url("data:image/svg+xml,%3Csvg width='17' height='17' viewBox='0 0 17 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13.7504 4.48962L14.6932 5.43243L7.08376 13.0419L2.80762 8.76577L3.75043 7.82296L7.08376 11.1563L13.7504 4.48962Z' fill='%23057164'/%3E%3C/svg%3E%0A");
            &::before {
                content: "";
                display: inline-block;
                width: 12px;
                height: 9px;
                background-size: cover;
                // background-color: $info-ld;
            }
        }
        
        &-info {
            padding-left: 14px;
            padding-top: 8px;
            h3 {
                font-size: 16px;
                line-height: 20px;
                margin-bottom: 8px;
            }
        }
    }
    &.active {
        display: block;
        animation: fadeIn 0.35s cubic-bezier(0.39, 0.575, 0.565, 1);
    }
    &s {
        &-content {
            max-width: 100%;
            width: 838px;
        }
        &-nav {
            max-width: 245px;
            margin: 0 auto;
            padding: 10px 0;
            ul {
                display: flex;
                list-style: none;
                padding: 0;
                margin: 0;
                justify-content: space-between;
                overflow: hidden;
                position: relative;
                z-index: 1;
                &::before {
                    content: "";
                    display: block;
                    height: 1px;
                    left: 0;
                    right: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    background-color: #d9d9d9;
                    position: absolute;
                    z-index: -1;
                }
                li {
                    width: 32px;
                    height: 32px;
                    display: flex;
                    align-items: center;
                    align-content: center;
                    justify-content: center;
                    border-radius: 50%;
                    background: #d2d2d3;
                    transition: 0.25s;
                    box-shadow: 0 0 0 10px $white;
                    cursor: pointer;
                    &.active {
                        background-color: $info-ld;
                        color: $white;
                    }
                    &.completed {
                        color: transparent;
                        background-color: #d7f4ee;
                        background-position: center center;
                        background-size: 70%;
                        background-repeat: no-repeat;
                        background-image: url("data:image/svg+xml,%3Csvg width='17' height='17' viewBox='0 0 17 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13.7504 4.48962L14.6932 5.43243L7.08376 13.0419L2.80762 8.76577L3.75043 7.82296L7.08376 11.1563L13.7504 4.48962Z' fill='%23057164'/%3E%3C/svg%3E%0A");
                        &::before {
                            content: "";
                            display: inline-block;
                            width: 12px;
                            height: 9px;
                            background-size: cover;
                            // background-color: $info-ld;
                        }
                    }
                }
            }
        }
    }
}

.side-tabs-nav {
    max-width: 350px;
    line-height: 1.7;
    position: relative;
    z-index: 2;
    .tab-step {
        cursor: pointer;
    }
    &::before {
        content: "";
        display: block;
        width: 1px;
        left: 15px;
        top: 0;
        bottom: 0;
        background-color: #d9d9d9;
        position: absolute;
        z-index: -1;
    }
    > div:last-child {
        position: relative;
        .tab-step {
            &::before {
                display: block;
                content: "";
                position: absolute;
                background: $grey;
                width: 5px;
                height: 100%;
                top: 0;
                z-index: -1;
            }
        }
    }
}
