.container {
    padding: 0px 15px;
    margin: 0 auto;
    max-width: 1440px;
}

.row {
    margin: 0 -15px;
    display: flex;
    flex-wrap: wrap;
}

[class^="col"] {
    padding: 0px 15px;
}

.col-12 {
    width: 100%;
    flex-basis: 100%;
}

.col-6 {
    width: 50%;
    flex-basis: 50%;
}

html,
body {
    min-height: 100%;
    @media screen and (min-width: 768px) {
        height: 100%;
    }
}
.wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
}
.content {
    flex: 1 0 auto;
}
.footer {
    flex: 0 0 auto;
}

.d-flex {
    display: flex;
}
.jc-sb {
    justify-content: space-between;
}
.jc-c {
    justify-content: center;
}
.ai-c {
    align-items: center;
}
.pull-right {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
}
.flex-wrap {
    flex-wrap: wrap;
}

.pull-center {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@media screen and (min-width: 768px) {
    .col-md-6 {
        width: 50%;
        flex-basis: 50%;
    }
}
