.alert {
    display: inline-block;
    padding: 0px 8px 0px 24px;
    position: relative;
    font-size: 14px;
    line-height: 24px;
    border-radius: 100px;
    &::before {
        content: "";
        top: 50%;
        left: 5px;
        transform: translateY(-50%);
        background-size: contain;
        width: 14px;
        height: 14px;
        display: inline-block;
        position: absolute;
        shape-outside: circle(50%);
    }
    &.info {
        background-color: $info-l;
        color: $info;
        &::before {
            background-image: url("data:image/svg+xml;base64,");
        }
    }
    &.simple {
        color: $font-color-secondary;
        &::before {
            display: none;
        }
    }
}
