.w-100 {
  width: 100%;
  flex-basis: 100%;
}

$bgs: (
  "white": $white,
  "info": $info,
  "success": $success,
  "grey": $grey,
  "danger": $danger
);

@each $name, $color in $bgs {
  .bg-#{$name} {
    background-color: $color;
  }
  .text-color-#{$name} {
    color: $color;
  }
}

$sizes: (
  "1": 10px,
  "2": 16px,
  "3": 20px,
  "4": 24px,
  "5": 28px,
  "6": 36px,
  "7": 40px,
);

@each $name, $size in $sizes {
  .pb-#{$name} {
    padding-bottom: $size;
  }
  .p-#{$name} {
    padding: $size;
  }
  .pr-#{$name} {
    padding-right: $size;
  }
  .pt-#{$name} {
    padding-top: $size;
  }
  .mb-#{$name} {
    margin-bottom: $size;
  }
  .mt-#{$name} {
    margin-top: $size;
  }
  .mr-#{$name} {
    margin-right: $size;
  }
}

@media screen and (max-width: 767px) {
  .hide-mobile {
    display: none !important;
  }
}

@media screen and (min-width: 768px) {
  .hide-pc {
    display: none !important;
  }
}

.text-center{
  text-align: center;
}
.disable-click{
  pointer-events: none;
}