.form-controller {
    display: block;
    position: relative;
    transition: cubic-bezier(0.39, 0.575, 0.565, 1) 0.3s;
    &.checkbox {
        display: flex;
        cursor: pointer;
        input[type="checkbox"] {
            width: 18px;
            height: 18px;
            margin: 0px 27px;
            // -webkit-appearance: none;
            // appearance: none;
            // padding: 0;
            // display: inline-block;
            // border-radius: 0;
        }
    }
    .message {
        color: $font-color-secondary;
        font-size: 12px;
        padding-top: 8px;
        padding-left: 16px;
        padding-bottom: 8px;
        // position: absolute;
        position: relative;
        padding-bottom: 8px;
        &::before {
            content: "";
            display: inline-block;
            background-repeat: no-repeat;
            position: absolute;
            left: 0;
            top: 10px;
            width: 12px;
            height: 12px;
            background-size: contain;
            background-image: url("data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M6.00033 11.8333C2.77866 11.8333 0.166992 9.22162 0.166992 5.99996C0.166992 2.7783 2.77866 0.166626 6.00033 0.166626C9.22199 0.166626 11.8337 2.7783 11.8337 5.99996C11.8337 9.22162 9.22199 11.8333 6.00033 11.8333ZM5.41699 3.08329V6.58329H6.58366V3.08329H5.41699ZM6.00033 8.91663C5.59762 8.91663 5.27116 8.59017 5.27116 8.18746C5.27116 7.78475 5.59762 7.45829 6.00033 7.45829C6.40303 7.45829 6.72949 7.78475 6.72949 8.18746C6.72949 8.59017 6.40303 8.91663 6.00033 8.91663Z' fill='%23656772'/%3E%3C/svg%3E%0A");
        }
    }
    &.with-icon {
        input {
            padding-left: 48px;
        }
        &.prefix {
            input {
                padding-left: 86px;
            }

            .icon {
                max-width: 73px;
                transform: translateY(-34px);
                left: 10px;
            }
        }
    }
    &.toched {
        &.valid {
            input {
                padding-right: 48px;
                border-color: $success;
            }
            &::after {
                content: "";
                display: block;
                position: absolute;
                right: 16px;
                width: 15px;
                height: 11px;
                bottom: 18px;
                background-repeat: no-repeat;
                background-size: contain;
                background-image: url("data:image/svg+xml;base64,");
                animation: scale 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
            }
        }
        &.invalid {
            input {
                padding-right: 48px;
                border-color: $danger;
            }
            .message {
                color: $danger;
                &::before {
                    background-image: url("data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M6.00033 11.8333C2.77866 11.8333 0.166992 9.22162 0.166992 5.99996C0.166992 2.7783 2.77866 0.166626 6.00033 0.166626C9.22199 0.166626 11.8337 2.7783 11.8337 5.99996C11.8337 9.22162 9.22199 11.8333 6.00033 11.8333ZM5.41699 3.08329V6.58329H6.58366V3.08329H5.41699ZM6.00033 8.91663C5.59762 8.91663 5.27116 8.59017 5.27116 8.18746C5.27116 7.78475 5.59762 7.45829 6.00033 7.45829C6.40303 7.45829 6.72949 7.78475 6.72949 8.18746C6.72949 8.59017 6.40303 8.91663 6.00033 8.91663Z' fill='%23D7424B'/%3E%3C/svg%3E%0A");
                }
            }
        }
    }
    &.invalid:not(.toched) {
        .message {
            display: none;
        }
    }
    .icon {
        max-width: 20px;
        max-height: 15px;
        position: absolute;
        left: 16px;
        transform: translateY(16px);
    }

    input,
    textarea {
        height: 48px;
        border: none;
        width: 100%;
        outline: none;
        padding: 12px 24px 12px 16px;
        font-size: 16px;
        line-height: 1.5;
        color: $font-color;
        border-radius: 8px;
        transition: 0.3s;
        background-color: rgba(101, 103, 114, 0.08);
        border: 2px solid rgba(101, 103, 114, 0.08);
        &:focus {
            border: 2px solid rgba(101, 103, 114, 0.5);
        }
    }
    textarea {
        height: 68px;
    }
    &--label {
        display: block;
        margin-bottom: 8px;
        line-height: 1.5;
        &.required {
            &::after {
                content: "* ";
                height: 6px;
                right: 0px;
                margin-left: 3px;
                margin-bottom: 6px;
                width: 6px;
                display: inline-block;
                background-size: contain;
                background-repeat: no-repeat;
                color: $danger;
                // background-image: url("data:image/svg+xml,%3Csvg width='6' height='6' viewBox='0 0 6 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2.38684 5.66665H3.60423L3.51313 3.83532L5.04522 4.79918L5.66634 3.87548L3.95206 2.99998L5.66634 2.11645L5.04522 1.20078L3.51313 2.16464L3.60423 0.333313H2.38684L2.47794 2.15661L0.954126 1.20078L0.333008 2.11645L2.05558 2.99998L0.333008 3.89155L0.954126 4.80721L2.47794 3.85139L2.38684 5.66665Z' fill='%23D7424B'/%3E%3C/svg%3E%0A");
            }
        }
    }
}
