.overlay {
    background: rgba(19, 20, 22, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    .modal {
        min-height: 216px;
        background-color: $white;
        color: $font-color-secondary;
        border-radius: 12px;
        box-shadow: 0px 2px 6px 0px rgba(19, 20, 22, 0.15);
        padding: 16px;
        max-width: 100%;
        width: 640px;
        min-width: 300px;
        @media screen and (min-width: 768px) {
            padding: 60px;
        }
        .btn{
            width: 343px;
            max-width: 90%;
        }
    }
}
