[data-tooltip]{
   cursor: pointer; 
}
.tooltip{
   position: fixed;
   left: 0;
   right: 0;
   bottom: 0;
   top: 0;

   display: flex;
   justify-content: center;
   align-items: center;
   z-index: 100;
   transform: scale(0);
   opacity: 0;
   transition: 0.35s cubic-bezier(0.39, 0.575, 0.565, 1);
   transform-origin: center center;
   &.active{
      // animation: fadeInScale 0.35s cubic-bezier(0.39, 0.575, 0.565, 1) forwards;
      transform: scale(1);
      opacity: 1;
   }
   .card{
      color: $font-color;
      max-width: 100%;
      svg{
         max-width: 100%;
      }
   }
}
