.btn {
    padding: 16px 2px;
    border-radius: 12px;
    border-width: 1px;
    display: inline-block;
    cursor: pointer;
    border: 1px solid;
    min-width: 160px;
    text-align: center;
    // border: 1px solid;
    font-size: 16px;
    line-height: 16px;
    color: $font-color;
    text-decoration: none;
    border: 1px solid;
    border-image-source: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.075) 0%,
        rgba(0, 0, 0, 0.1) 89.06%,
        rgba(0, 0, 0, 0.2) 100%
    );
    background-color: $white;
    box-shadow: 0px 2px 6px 0px rgb(19 20 22 / 15%);

    &[disabled] {
        background: rgba(143, 145, 153, 0.2) !important;
        cursor: not-allowed;
    }

    &.primary {
        background: $primary;
        border: 1px solid;

        border-image-source: linear-gradient(
            180deg,
            rgba(0, 0, 0, 0.075) 0%,
            rgba(0, 0, 0, 0.1) 89.06%,
            rgba(0, 0, 0, 0.2) 100%
        );
    }
}
